.main-container {
  width: 100%;
  height: auto;
}

.contacts-container {
  width: 100%;
  height: auto;
  position: relative;
}

.pop-up {
  position: relative;
  display: inline-block;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.home-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 10vh;
}

.home-buttons__container {
  position: relative;
  margin: 10px;
}

.home-buttons__popup {
  width: 50%;
  position: absolute;
  bottom: 125%;
  right: 0;
  left: 0;
  margin: auto;
}

.home {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: auto;
  background-image: url("../media/img/Web-Development-Project.jpg");
  background-position: center;
  background-size: cover;
}

.home__content {
  max-width: 80%;
  align-self: center;
  margin-top: 25vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.home__title {
  font-size: 60px;
  font-weight: 500;
  color: white;
  letter-spacing: 3pt;
  font-family: "Open Sans", sans-serif;
  max-width: 90vw;
  margin-bottom: 0;
}

.home__description {
  font-size: 20px;
  font-weight: normal;
  color: white;
  letter-spacing: 1pt;
  font-family: "Open Sans", sans-serif;
  margin-top: 15px;
}

.home__angle {
  margin-top: 5vh;
  font-size: 4vh;
  margin-bottom: 5vh;
  transition-duration: 300ms;
  padding: 30px;
  border-radius: 50%;
  color: white;
}
.home__angle:hover {
  cursor: pointer;
  transform: scale(1.3);
  transition-duration: 300ms;
}

@media (max-width: 450px) {
  .home__content {
    margin-top: 20vh;
  }

  .home__title {
    font-size: 35px;
    letter-spacing: 1pt;
  }

  .home__description {
    font-size: 15px;
  }

  .home__angle {
    margin-top: 8vh;
    margin-bottom: 6vh;
  }
}
.header {
  position: fixed;
  width: 100%;
  min-height: 70px;
  height: auto;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 17px;
  font-weight: 500;
  color: black;
  letter-spacing: 1px;
  font-family: "Open Sans", sans-serif;
  z-index: 2;
}

.header__head {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__head p {
  font-size: 20px;
  font-weight: bold;
  color: black;
  letter-spacing: 0px;
  font-family: "Open Sans", sans-serif;
  margin-left: 5vw;
}

.header__bar {
  display: none;
}

.header__menu {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
  list-style: none;
  margin-right: 5vw;
}

.header__item {
  margin-right: 30px;
}

.header__link {
  color: black;
  text-decoration: none;
  transition: 300ms;
}
.header__link:hover {
  color: #00897b;
  cursor: pointer;
}

.header__link_colored {
  color: #00897b;
}

.header__search {
  font-size: 15px;
  font-weight: 100;
  color: black;
  letter-spacing: 0px;
  font-family: "Open Sans", sans-serif;
  width: auto;
  max-width: 120px;
  margin-right: 2px;
  outline: none;
  border: none;
  border-radius: 0;
  border-bottom: 1px solid black;
}

.header__search_hidden {
  display: none;
}

@keyframes slide {
  0% {
    height: 50px;
  }
  100% {
    height: auto;
  }
}
@media (max-width: 900px) {
  .header {
    min-height: 50px;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
  }

  .header__menu, .item-list {
    display: none;
  }

  .header__menu_show {
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  .header__menu_show > li {
    margin-bottom: 15px;
  }

  .header__bar {
    display: inline;
    font-size: 25px;
    margin-right: 15vw;
    animation-duration: 1s;
  }

  .header__bar_hidden {
    display: none !important;
  }

  .header__search_hidden {
    display: inline;
  }
}
.about-item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  justify-self: center;
  width: 250px;
}
.about-item h3 {
  font-size: 17px;
  font-weight: bold;
  color: #000;
  letter-spacing: 0.4px;
  font-family: "Open Sans", sans-serif;
  margin: 5px 0 15px 0;
}
.about-item p {
  font-size: 13px;
  font-weight: normal;
  color: #000;
  letter-spacing: 0px;
  font-family: "Open Sans", sans-serif;
  margin-bottom: 0;
  margin-top: 0;
}
.about-item:nth-child(2) {
  margin: 20px;
}

.about-item__img {
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
}

.about {
  background-color: white;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(6, minmax(80px, -webkit-max-content));
  grid-template-rows: repeat(6, minmax(80px, max-content));
}
@media (max-width: 700px) {
  .about {
    grid-template-rows: repeat(6, minmax(60px, -webkit-max-content));
    grid-template-rows: repeat(6, minmax(60px, max-content));
  }
}

.about__info {
  grid-column: 2/10;
  grid-row: 2/3;
}

.about__content {
  grid-column: 2/10;
  grid-row: 4/6;
  text-align: center;
  list-style: none;
  list-style-position: inside;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}

.infoblock {
  background-color: white;
  padding: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transition: 500ms;
  text-align: center;
  justify-self: center;
  max-width: 300px;
}
.infoblock h3 {
  font-size: 17px;
  font-weight: bold;
  color: inherit;
  letter-spacing: 0.4px;
  font-family: "Open Sans", sans-serif;
  margin: 5px 0 15px 0;
}
.infoblock p {
  font-size: 13px;
  font-weight: normal;
  color: inherit;
  letter-spacing: 0px;
  font-family: "Open Sans", sans-serif;
  margin-bottom: 0;
  margin-top: 0;
}
.infoblock:hover {
  background-color: #00897b;
  color: white;
  box-shadow: 0 7px 20px 0 rgba(0, 0, 0, 0.25);
  border-color: white;
}

.infoblock__button {
  font-size: 17px;
  font-weight: bold;
  color: inherit;
  letter-spacing: 0px;
  font-family: "Open Sans", sans-serif;
  margin-top: 50px;
  width: auto;
  height: 50px;
  border: 2px solid black;
  padding: 0 15px;
  border-color: inherit;
  background-color: transparent;
}
.infoblock__button:hover {
  cursor: pointer;
}

.infoblock__icon {
  width: 80px;
  height: 80px;
  z-index: 1;
  font-size: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.services {
  background-color: #e4fdf9;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(6, minmax(80px, -webkit-max-content));
  grid-template-rows: repeat(6, minmax(80px, max-content));
}
@media (max-width: 700px) {
  .services {
    grid-template-rows: repeat(6, minmax(60px, -webkit-max-content));
    grid-template-rows: repeat(6, minmax(60px, max-content));
  }
}

.services__info {
  grid-column: 2/10;
  grid-row: 2/3;
}

.services__content {
  grid-column: 2/10;
  grid-row: 4/6;
  text-align: center;
  list-style: none;
  list-style-position: inside;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 30px;
}
@media (max-height: 500px) {
  .services__content {
    grid-template-columns: repeat(auto-fit, minmax(70vw, 1fr));
  }
}

.portfolio__item {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.portfolio__item img {
  width: 100%;
}

.portfolio__item_hover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: rgba(0, 0, 0, 0);
  transition: 0.5s;
}
.portfolio__item_hover:hover {
  background-color: rgba(0, 0, 0, 0.7);
  text-align: center;
  justify-self: center;
  cursor: pointer;
}
.portfolio__item_hover:hover h3 {
  font-size: 17px;
  font-weight: bold;
  color: white;
  letter-spacing: 0.4px;
  font-family: "Open Sans", sans-serif;
  margin: 5px 0 15px 0;
}
.portfolio__item_hover:hover p {
  font-size: 13px;
  font-weight: normal;
  color: white;
  letter-spacing: 0px;
  font-family: "Open Sans", sans-serif;
  margin-bottom: 0;
  margin-top: 0;
}

.portfolio__switcher {
  grid-column: 2/10;
  grid-row: 4/5;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.portfolio__button {
  margin: 10px 10px;
}
@media (max-width: 400px) {
  .portfolio__button {
    margin: 5px 5px;
  }
}
.portfolio__button:hover {
  color: black !important;
  background-color: transparent !important;
}

.portfolio__button_selected, .portfolio__button_selected:hover {
  color: #00897b !important;
}

.portfolio {
  background-color: white;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(9, minmax(80px, -webkit-max-content));
  grid-template-rows: repeat(9, minmax(80px, max-content));
}

.portfolio__info {
  grid-column: 2/10;
  grid-row: 2/3;
}

.portfolio__list {
  grid-column: 2/10;
  grid-row: 6/7;
  list-style: none;
  list-style-position: inside;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20vw, 1fr));
  grid-gap: 30px;
}
@media (max-width: 700px) {
  .portfolio__list {
    grid-template-columns: repeat(auto-fit, minmax(70vw, 1fr));
  }
}

.portfolio__show-all {
  grid-column: 2/10;
  grid-row: 8/9;
  align-self: center;
  justify-self: center;
}

.tariff {
  min-width: 300px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(22, minmax(25px, -webkit-max-content));
  grid-template-rows: repeat(22, minmax(25px, max-content));
  grid-gap: 5px;
  background-color: white;
  transition: 0.4s;
}
.tariff:nth-child(2) {
  margin: 20px;
}
@media (max-width: 700px) {
  .tariff {
    min-width: 250px;
  }
  .tariff:nth-child(2) {
    margin: 50px 0;
  }
}
@media (max-height: 900px) {
  .tariff {
    grid-template-rows: repeat(22, minmax(20px, -webkit-max-content));
    grid-template-rows: repeat(22, minmax(20px, max-content));
  }
}
.tariff:hover {
  box-shadow: 0px 5px 20px 5px rgba(0, 0, 0, 0.1);
}

.tariff__header {
  width: 100%;
  grid-column: 1/5;
  grid-row: 1/8;
  background-color: #3949ab;
  -webkit-mask: url("../media/svg/Frame.svg") bottom left/cover;
          mask: url("../media/svg/Frame.svg") bottom left/cover;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  justify-self: center;
}

.tariff__title {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 25px;
  font-weight: normal;
  color: white;
  letter-spacing: 0.5px;
  font-family: "Open Sans", sans-serif;
}

.tariff__price {
  font-size: 15px;
  font-weight: normal;
  color: white;
  letter-spacing: 0.5px;
  font-family: "Open Sans", sans-serif;
}
.tariff__price span {
  font-size: 50px;
}
.tariff__price span sup {
  font-size: 25px;
}

.tariff__features-list {
  grid-column: 2/4;
  grid-row: 8/17;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  list-style: none;
  list-style-position: inside;
  margin: 0;
  padding: 0;
  margin-top: 40px;
}

.tariff__feature {
  text-align: center;
  font-size: 15px;
  font-weight: normal;
  color: #000;
  letter-spacing: 0.5px;
  font-family: "Open Sans", sans-serif;
  border-bottom: 2px solid #bfe2de;
  width: 100%;
  padding-bottom: 10px;
}

.tariff__order-button {
  grid-column: 2/4;
  grid-row: 17/23;
  align-self: center;
  width: 100% !important;
  height: 55px !important;
  font-size: 17px !important;
}

.price {
  width: 100%;
  background-color: #f0f1f1;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(5, minmax(80px, -webkit-max-content));
  grid-template-rows: repeat(5, minmax(80px, max-content));
}

.price__info {
  grid-column: 2/10;
  grid-row: 2/3;
}

.price__content {
  background-color: transparent;
  grid-column: 2/10;
  grid-row: 4/5;
  list-style: none;
  list-style-position: inside;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}
@media (max-aspect-ratio: 1/1) {
  .price__content {
    grid-template-columns: repeat(auto-fit, minmax(70vw, 1fr));
  }
}

.price__item {
  width: 30%;
}
@media (max-width: 700px) {
  .price__item {
    width: 80%;
  }
}

.blog-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 750px) {
  .blog-item {
    flex-direction: column;
  }
}

.blog-item__content, .blog-item > img {
  width: 49%;
}
@media (max-width: 750px) {
  .blog-item__content, .blog-item > img {
    width: 100%;
  }
}

.blog-item__content {
  align-self: flex-start;
  margin-top: 40px;
}
@media (max-width: 750px) {
  .blog-item__content {
    margin: 0;
  }
}

.blog-item__title {
  font-size: 20px;
  font-weight: 500;
  color: #000;
  letter-spacing: 0.5px;
  font-family: "Open Sans", sans-serif;
  transition: 0.3s;
}
.blog-item__title:hover {
  color: #00897b;
  cursor: pointer;
}

.blog-item__key {
  font-weight: 500;
  width: -webkit-max-content;
  width: max-content;
  margin: 0 20px 0 0;
}

.blog-item__description {
  font-size: 14px;
  font-weight: normal;
  color: #000;
  letter-spacing: 0.5px;
  font-family: "Open Sans", sans-serif;
}

#blog {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(7, minmax(80px, -webkit-max-content));
  grid-template-rows: repeat(7, minmax(80px, max-content));
  background-color: #e4fdf9;
}
@media (max-width: 700px) {
  #blog {
    grid-template-rows: repeat(7, minmax(60px, -webkit-max-content));
    grid-template-rows: repeat(7, minmax(60px, max-content));
  }
}

.blog__info {
  grid-column: 2/10;
  grid-row: 2/3;
}

.blog__content {
  grid-column: 2/10;
  grid-row: 4/5;
  list-style: none;
  list-style-position: inside;
  margin: 0;
  padding: 0;
}
@media (min-width: 750px) {
  .blog__content *:nth-child(even) {
    flex-direction: row-reverse;
  }
}
@media (max-width: 750px) {
  .blog__content li {
    margin-bottom: 30px;
  }
  .blog__content *:last-child {
    margin: 0;
  }
}

.blog__button {
  grid-column: 3/9;
  grid-row: 6/7;
  align-self: center;
  justify-self: center;
}

.person-card {
  min-width: 250px;
  min-height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  margin: 10px;
}
@media (max-width: 700px) {
  .person-card {
    width: 100%;
  }
  .person-card:nth-child(2) {
    margin: 20px 0;
  }
}
.person-card img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}

.person-card__info {
  text-align: center;
  justify-self: center;
}
.person-card__info h3 {
  font-size: 17px;
  font-weight: bold;
  color: #000;
  letter-spacing: 0.4px;
  font-family: "Open Sans", sans-serif;
  margin: 5px 0 15px 0;
}
.person-card__info p {
  font-size: 13px;
  font-weight: normal;
  color: #000;
  letter-spacing: 0px;
  font-family: "Open Sans", sans-serif;
  margin-bottom: 0;
  margin-top: 0;
}

.person-card__name {
  margin: 20px 0 8px 0;
}

.person-card__socials {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 50px;
}

.block-switcher {
  grid-column: 5/7;
  grid-row: 6/7;
  display: flex;
  justify-content: center;
  align-items: center;
}

.block-switcher__item {
  fill: #00897b;
}
@media (min-width: 500px) {
  .block-switcher__item:hover {
    cursor: pointer;
    fill: #007064;
  }
}

.block-switcher__item_active {
  fill: #3949ab;
}

.team {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: 20px repeat(6, minmax(80px, -webkit-max-content));
  grid-template-rows: 20px repeat(6, minmax(80px, max-content));
}
@media (max-width: 700px) {
  .team {
    grid-template-rows: 50px repeat(6, minmax(60px, -webkit-max-content));
    grid-template-rows: 50px repeat(6, minmax(60px, max-content));
  }
}

.team__info {
  grid-column: 2/10;
  grid-row: 3/4;
}

.team__content {
  grid-column: 2/10;
  grid-row: 5/6;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}

.team__person-card {
  width: 30%;
}

.team__content_hidden {
  display: none;
}

.contacts-form {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, minmax(40px, -webkit-max-content));
  grid-template-rows: repeat(3, minmax(40px, max-content));
  grid-gap: 30px;
}

.contacts-form__userinfo {
  margin-top: 50px;
  grid-column: 1/5;
  grid-row: 1/2;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 700px) {
  .contacts-form__userinfo {
    flex-direction: column;
  }
}

.contacts-form__name, .contacts-form__email, .contacts-form__message {
  font-size: 13px;
  font-weight: 500;
  color: #000;
  letter-spacing: 0.3pt;
  font-family: "Open Sans", sans-serif;
  outline: none;
  border-radius: 5px;
  border: none;
  padding: 0 20px;
}
@media (max-width: 700px) {
  .contacts-form__name, .contacts-form__email, .contacts-form__message {
    padding: 0 5%;
  }
}

.contacts-form__message {
  padding: 20px;
}
@media (max-width: 700px) {
  .contacts-form__message {
    padding: 20px 5%;
  }
}

.contacts-form__name, .contacts-form__email {
  width: 50%;
  margin: 0;
  height: 55px;
}
@media (max-width: 700px) {
  .contacts-form__name, .contacts-form__email {
    min-width: 90%;
    margin: 0;
  }
}

.contacts-form__name {
  margin-right: 15px;
}
@media (max-width: 700px) {
  .contacts-form__name {
    margin-right: 0px;
    margin-bottom: 15px;
  }
}

.contacts-form__email {
  margin-left: 15px;
}
@media (max-width: 700px) {
  .contacts-form__email {
    margin-left: 0px;
    margin-top: 15px;
  }
}

.contacts-form__message {
  grid-column: 1/5;
  grid-row: 2/3;
  min-height: 150px;
  resize: none;
}

.contacts-form__send-button {
  grid-column: 4/4;
  grid-row: 3/4;
  position: relative;
}

.contacts-form__popup {
  width: 80%;
  position: absolute;
  bottom: 125%;
  right: 0;
}

.contacts-form__button {
  width: 100%;
  min-width: 100px;
  border-radius: 5px;
  transition: 0.3s;
  height: 55px;
  padding: 0 20px;
  background-color: white;
  outline: none;
  border: none;
  font-size: 15px;
  font-weight: 500;
  color: black;
  letter-spacing: 0.5px;
  font-family: "Open Sans", sans-serif;
}
.contacts-form__button:hover {
  cursor: pointer;
  background-color: transparent;
  border: 2px solid white;
  color: white;
}

.list-item {
  font-size: 15px;
  font-weight: 500;
  color: white;
  letter-spacing: 0.5px;
  font-family: "Open Sans", sans-serif;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-width: 200px;
}

.list-item__background {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  width: 100px;
  background-color: #3949ab;
  justify-self: center;
  border-radius: 50px;
  font-size: 40px;
}

.contacts {
  width: 100%;
  background-image: url("../media/img/ContactsBack.jpg");
  background-position: center;
  background-size: cover;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(5, minmax(80px, -webkit-max-content));
  grid-template-rows: repeat(5, minmax(80px, max-content));
  position: relative;
}
@media (max-width: 700px) {
  .contacts {
    grid-template-rows: repeat(5, minmax(60px, -webkit-max-content));
    grid-template-rows: repeat(5, minmax(60px, max-content));
  }
}

.contacts__info {
  grid-column: 2/8;
  grid-row: 2/3;
}
.contacts__info *:nth-child(n) {
  color: white;
}

.contacts__content {
  grid-column: 2/8;
  grid-row: 4/5;
}
.contacts__content .contacts__items {
  list-style: none;
  list-style-position: inside;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}
@media (max-aspect-ratio: 1/1) {
  .contacts__content .contacts__items > *:nth-child(n) {
    margin-bottom: 20px;
  }
}

.footer {
  bottom: 0;
  left: 0;
  min-height: 50px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  z-index: 5;
}
.footer p {
  font-size: 15px;
  font-weight: 500;
  color: #000;
  letter-spacing: 1pt;
  font-family: "Open Sans", sans-serif;
}