.title {
  max-width: 700px;
  text-align: center;
  justify-self: center;
}

.title__name {
  margin-top: 0;
  font-size: 25px;
  font-weight: bold;
  color: #000;
  letter-spacing: 0.5px;
  font-family: "Open Sans", sans-serif;
}

.title__description {
  font-size: 15px;
  font-weight: normal;
  color: #000;
  letter-spacing: 0.5px;
  font-family: "Open Sans", sans-serif;
}